import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, Params} from "@angular/router";
import {FormArray, FormControl, FormGroup, Validators} from "@angular/forms";
import {RecipeService} from "../recipe.service";
import {Recipe} from "../recipe.model";

@Component({
  selector: 'app-recipe-edit',
  templateUrl: './recipe-edit.component.html',
  styleUrls: ['./recipe-edit.component.css']
})
export class RecipeEditComponent implements OnInit {

  id: number;
  editMode = false;
   recipeForm: FormGroup;
   recipe = {name:'', imagePath:'', description:'', ingredients: []};
  recipeIngredients = new FormArray([]);
  ingredients;




  constructor(private activeRoute: ActivatedRoute, private recipeService: RecipeService) {
  }

  ngOnInit() {


    this.activeRoute.params.subscribe(
      (params: Params) => {
        this.id = +params['id'];
        this.editMode = params['id'] != null;
        this.recipeFormInit();

        console.log(this.recipeForm);
        this.ingredients = this.recipeForm.get('ingredients');
        this.ingredients = this.ingredients.controls;
        console.log(this.ingredients);

      }
    );

  }

  onSubmit(){
    console.log(this.recipeForm);
    const newRecipe = new Recipe(
      this.recipeForm.value['name'],
      this.recipeForm.value['description'],
      this.recipeForm.value['imagePath'],
      this.recipeForm.value['ingredients']);

    if(this.editMode)
      {this.recipeService.updateRecipe(this.id, newRecipe);}
      else{
        this.recipeService.addRecipe(newRecipe);
      }

  }

  recipeFormInit(){


    if(this.editMode){
      this.recipe = this.recipeService.getRecipeById(this.id);
      //console.log(this.recipe);
      if(this.recipe['ingredients']){
        for(let ingredient of this.recipe.ingredients){
          this.recipeIngredients.push(
            new FormGroup({
              'name': new FormControl(ingredient.name, Validators.required),
              'amount': new FormControl(ingredient.amount, Validators.required)
            })
          );
        }
      }

    }

    console.log(this.recipeForm);

    this.recipeForm = new FormGroup({
      'name' : new FormControl(this.recipe.name, Validators.required),
      'imagePath' : new FormControl(this.recipe.imagePath, Validators.required),
      'description' : new FormControl(this.recipe.description, Validators.required),
      'ingredients': this.recipeIngredients

    });


  }


  onAddIngredient(){
    (<FormArray>this.recipeForm.get('ingredients')).push( new FormGroup({
      'name' : new FormControl(),
      'amount' : new FormControl()
    }));
  }
}



