import {Component, Input, OnInit} from '@angular/core';
import {ShoppinglistService} from "../../shopping-list/shoppinglist.service";
import {ActivatedRoute, Params} from "@angular/router";
import {RecipeService} from "../recipe.service";
import {Recipe} from "../recipe.model";

@Component({
  selector: 'app-recipe-details',
  templateUrl: './recipe-details.component.html',
  styleUrls: ['./recipe-details.component.css']
})
export class RecipeDetailsComponent implements OnInit {

  recipeDetails: Recipe;
  id: number;


  constructor(private shoppingListService: ShoppinglistService, private activeRoute: ActivatedRoute, private recipeService: RecipeService) { }

  ngOnInit() {

    console.log();
    this.activeRoute.params.subscribe(
      (params: Params) => {
        this.id = +params['id'];
        this.recipeDetails = this.recipeService.getRecipeById(this.id);
      }
    );



  }

  onClickAddtoSL(){
    this.shoppingListService.addIngredientsFromRecipe(this.recipeDetails.ingredients);
  }

  onDeleteRecipe(){
    this.recipeService.deleteRecipe(this.id);
  }



}



